import { Component, Injector, OnInit } from '@angular/core';
import { FormArray, FormControl, FormGroup } from '@angular/forms';
import { URLz } from 'src/app/enums/url.enum';
import { Custom } from 'src/app/static/custom';
import { ImgType } from 'src/app/interface/common/img-type';
import { OperatingUnit } from 'src/app/interface/customizations/operating-unit-custom';
import { BaseForm } from 'src/app/class/base.form';
import { PartialSubmit } from 'src/app/interface/common/partial-submit';

@Component({
  selector: 'di-ou-add',
  templateUrl: './ou-add.component.html',
  styleUrls: ['./ou-add.component.css'],
})
export class OUAddComponent extends BaseForm implements OnInit {
  constructor(injector: Injector) {
    super(injector);

  }
  ngOnInit() {
    this._fhs._pathLocation = '/operating_unit/ou';
    this.param.endpoint = URLz.OU_CUSTOM;
    this.initForm();
    this._activeId = this._fhs._getURLParam('id');
    if (this._activeId) {
      this.patchData();
    }
  }
  initForm() {
    this._fs._form = this._fb.group({
      bg: ['', this._vs._vals('Business Group')],
      le: ['', this._vs._vals('Legal Entity')],
      ou: ['', this._vs._vals('Operating Unit')],
      customization: this._fb.array(
        [this.customization()],
        [
          this._vs.repeatTwoFields('organisation_id', 'system_id'),
          this._vs.repeatOneField('prefix')
        ]
      ),
      law: ['', this._vs._val('Law', {minChar: 4, maxChar:100, alpha: 1})],
      address: ['', this._vs._val('Address', {minChar: 4, maxChar:300, alphaNum: 1})],
      is_deposit: [false],
    });
  }
  data: any
  patchData() {
    this._http
      .get({ ...this.param, resource: this._activeId })
      .subscribe((res) => {
        const data: OperatingUnit = this.data= res.data.row;
        this._fs._form.patchValue({
          bg: data?.bg,
          le: data?.le,
          ou: data?.ou,
          law: data?.law,
          address: data?.address,
          is_deposit: data?.is_deposit,
        });
        if (data.customization.length > 0) {
          this.patchCustomization(data.customization);
        }
        if (data.law != '') {
          this._fs._form.markAllAsTouched();
        }
        this.imgLogo.link = data.logo;
        this.imgTop.link = data.top_image;
        this.imgWarn.link = data.warning_image;
        this.imgFooter.link = data.footer_image;
      });
  }
  // Images Access
  imgTop: ImgType = { display: 'Top Image' };
  imgLogo: ImgType = { display: 'Logo' };
  imgWarn: ImgType = { display: 'Warning Image' };
  imgFooter: ImgType = { display: 'Footer Image' };
  // 1 Customization Initialization
  customization(
    organization = '',
    system = '',
    prefix = ''
  ): FormGroup {
    return this._fb.group({
      organisation_id: [organization, this._vs._val('Organization', {isField:0})],
      system_id: [system, this._vs._val('System', {isField:0})],
      prefix: [prefix, this._vs._val('Prefix',{ minChar:4, maxChar:16, alpha: 1, hypenreg:1})],
    });
  }
  // 2 Customization (Template Iteration Purpose)
  get customizations() {
    return this._fs._form?.get('customization') as FormArray;
  }
  // 3 Customization (Adding New)
  addCustom() {
    const custom = this._fs._form.get('customization') as FormArray;
    custom.push(this.customization());
  }
  // 4 Customization (Removing Selected)
  rmvCustom(index: number) {
    const customizationArr = this._fs._form.get('customization') as FormArray;
    customizationArr.removeAt(index);
  }
  // 5 Customization (Updating Data)
  patchCustomization(
    customization: {
      organisation_id: string;
      system_id: string;
      prefix: string;
    }[]
  ) {
    const formArray = this._fs._form.get('customization') as FormArray;
    if (customization.length > 0) formArray.clear();
    customization.forEach((custom) => {
      formArray.push(
        this.customization(
          custom.organisation_id,
          custom.system_id,
          custom.prefix
        )
      );
    });
  }
  _onSubmity() {
    return super._onSubmity({
      validate: () => {
        return ( this._fs._form.invalid ||
          !this.imgFooter.link || !this.imgLogo.link ||
          !this.imgTop.link || !this.imgWarn.link ||
          this.imgFooter.error || this.imgLogo.error ||
          this.imgTop.error || this.imgWarn.error
        )
      },
      body: (ps: PartialSubmit ) => {
        const fd = new FormData();
        if (this.imgLogo.file) fd.append('logo', this.imgLogo.file);
        if (this.imgTop.file) fd.append('top_image', this.imgTop.file);
        if (this.imgWarn.file) fd.append('warning_image', this.imgWarn.file);
        if (this.imgFooter.file) fd.append('footer_image', this.imgFooter.file);
        if (this._activeId){
          this._fs._form.addControl('id', new FormControl(this._activeId));
        }
        Custom.jsontoFormData(this._fs._form.value, '', fd);
        ps.param.body = fd
      }
    })
  }
}
