import { Component, Injector, OnInit } from '@angular/core';
import { MatDialogConfig } from '@angular/material/dialog';
import { URLz } from 'src/app/enums/url.enum';
import { MatFullDialogComponent } from 'src/app/shared/components/dialogs/mat-full-dialog/mat-full-dialog.component';
import { ImgViewComponent } from 'src/app/shared/components/dialogs/img-view/img-view.component';
import { BaseListClass } from 'src/app/class/base-list-class';


@Component({
  selector: 'di-ou-list',
  templateUrl: './ou-list.component.html',
  styleUrls: ['./ou-list.component.css'],
})
export class OUListComponent
extends BaseListClass implements OnInit {

  constructor(public injector: Injector) {
    super(injector);
  }

  ngOnInit() {
    this._pathLocation = '/operating_unit/ou_add';
    this._component = 'Operating Unit';
    // this.initForm();
    this.initTables();


  }
  // initForm(){
  //   this._fs._form = this._fb.group({
  //     organisation_id: ['', this._vs._val('Oranaization')],
  //     system_id: ['', this._vs._val('System')]
  //   });
  // }
  initTables(){
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = [
      'id',
      'actions',// 'customization',
      'bg',
      'le',
      'ou',
      'law',
      'address',
      'is_deposit',
      'logo',
      'top_image',
      'warning_image',
      'footer_image'
    ];
    this._tbls[URLz.$].endpoint = URLz.OU_CUSTOM
    this._formCreator(this._tbls[URLz.$])
    this._refresh(URLz.$)
  }
  applyClass = { 'col-lg-6': true, 'col-md-6': true};
  openImage(data, title) {
    const config: MatDialogConfig = {
      panelClass: "dialog-responsive",
      data:  {source: data, title}
    }
    const dialogRef = this._dialog.open(
      ImgViewComponent, config);
    dialogRef.afterClosed().subscribe();
  }
  openDialog(data, title) {
    const config: MatDialogConfig = {
      panelClass: "dialog-responsive",
      data:  {source: data, title}
    }
    const dialogRef = this._dialog.open(
      MatFullDialogComponent, config);
    dialogRef.afterClosed().subscribe();
  }
}
