import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ACTION } from 'src/app/enums/action.enum';

import { OUAddComponent } from './ou-add/ou-add.component';
import { OUListComponent } from './ou-list/ou-list.component';

export const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: '',
        redirectTo: 'ou',
        pathMatch: 'full'
      },
      {
        path: "ou_add",
        component: OUAddComponent,
        data: {
          permission: [
            ACTION.EDIT
          ],
          condition: [
            {
              [ACTION.EDIT]: 'id'
            }
          ],
          title: "Operating Unit",
          urls: [
            { title: "Operating Unit"},
            { title: "Add" },
          ]
        }
      },
      {
        path: "ou",
        component: OUListComponent,
        data: {
          permission: [
            ACTION.VIEW
          ],
          title: "Operating Unit",
          urls: [
            { title: "Operating Unit" },
            { title: "List" },
          ],
        },
      },
    ]
  }
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OperatingUnitRoutingModule { }
